import React, {
  useState,
  useCallback,
  useEffect,
  useReducer,
  useLayoutEffect
} from "react";
import { Pane, Tablist, Tab, Paragraph, Text } from "evergreen-ui";
import WriteCharactorSearch from "@components/WriteCharactor/WriteCharactorSearch";
import chinese from "@constants/chinese.json";
import { converttoNoTone } from "@utils/utils";
import VoiceComponent from "@components/icons/VoiceComponent";
let time = 0;
let reqFrame;
let paths = [];
const BOARD_CELL_SIZE = 298;
const BOARD_SIZE = 3;
const PADDING = 2;
const LAYOUT_SIZE = 600;
const commonWords = ``;

export default function () {
  const [char, setChar] = useState("");
  const [pinyin, setPinyin] = useState("");
  const [state, setState] = useReducer((state, action) => {
    return {
      ...state,
      ...action
    }
  }, {
      selectedIndex: 0,
      tabs: ['Hiragana', 'Katakana'],
      Hiragana: [[
        ["あ", "い", "う", "え", "お"],
        ["か", "き", "く", "け", "こ"],
        ["さ", "し", "す", "せ", "そ"],
        ["た", "ち", "つ", "て", "と"],
        ["な", "に", "ぬ", "ね", "の"],
        ["は", "ひ", "ふ", "へ", "ほ"],
        ["ま", "み", "む", "め", "も"],
        ["や", "ゆ", "よ"],
        ["ら", "り", "る", "れ", "ろ"],
        ["わ", "を"],
        ["ん"]
      ],
      [
        ["が", "ぎ", "ぐ", "げ", "ご"],
        ["ざ", "じ", "ず", "ぜ", "ぞ"],
        ["だ", "ぢ", "づ", "で", "ど"],
        ["ば", "び", "ぶ", "べ", "ぼ"],
        ["ぱ", "ぴ", "ぷ", "ぺ", "ぽ"],
        ["きゃ", "きゅ", "きょ"],
        ["しゃ", "しゅ", "しょ"],
        ["ちゃ", "ちゅ", "ちょ"],
        ["にゃ", "にゅ", "にょ"],
        ["ひゃ", "ひゅ", "ひょ"],
        ["みゃ", "みゅ", "みょ"],
        ["りゃ", "りゅ", "りょ"],
        ["ぎゃ", "ぎゅ", "ぎょ"],
        ["じゃ", "じゅ", "じょ"],
        ["びゃ", "びゅ", "びょ"],
        ["ぴゃ", "ぴゅ", "ぴょ"]
      ]
      ],
      Katakana: [[
        ["ア", "イ", "ウ", "エ", "オ"],
        ["カ", "キ", "ク", "ケ", "コ"],
        ["サ", "シ", "ス", "セ", "ソ"],
        ["タ", "チ", "ツ", "テ", "ト"],
        ["ナ", "ニ", "ヌ", "ネ", "ノ"],
        ["ハ", "ヒ", "フ", "ヘ", "ホ"],
        ["マ", "ミ", "ム", "メ", "モ"],
        ["ヤ", "ユ", "ヨ"],
        ["ラ", "リ", "ル", "レ", "ロ"],
        ["ワ", "ヲ"],
        ["ン"],
      ],
      [
        ["ガ", "ギ", "グ", "ゲ", "ゴ"],
        ["ザ", "ジ", "ズ", "ゼ", "ゾ"],
        ["ダ", "ヂ", "ヅ", "デ", "ド"],
        ["バ", "ビ", "ブ", "ベ", "ボ"],
        ["パ", "ピ", "プ", "ペ", "ポ"],
        ["キャ", "キュ", "キョ"],
        ["シャ", "シュ", "ショ"],
        ["チャ", "チュ", "チョ"],
        ["ニャ", "ニュ", "ニョ"],
        ["ヒャ", "ヒュ", "ヒョ"],
        ["ミャ", "ミュ", "ミョ"],
        ["リャ", "リュ", "リョ"],
        ["ギャ", "ギュ", "ギョ"],
        ["ジャ", "ジュ", "ジョ"],
        ["ビャ", "ビュ", "ビョ"],
        ["ピャ", "ピュ", "ピョ"]
      ]
      ]
    })
  const onSearch = slug => {
    if (char && slug == char) {
      location.reload();
    }
    cancelAnimationFrame(reqFrame);
    paths = [];

    if (slug) {
      slug = slug.slice(0, 1);
      if (chinese[slug]) {
        paths = JSON.parse(JSON.stringify(chinese[slug]));
        location.hash = "#/" + slug;
        setChar(slug);
      }
    } else {
      paths = [];
      if (window.history.pushState) {
        window.history.pushState("", "/", window.location.pathname);
      } else {
        window.location.hash = "";
      }
      setChar("");
    }
  };

  const toTop = () => {
    try {
      // trying to use new API - https://developer.mozilla.org/en-US/docs/Web/API/Window/scrollTo
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth"
      });
    } catch (error) {
      // just a fallback for older browsers
      window.scrollTo(0, 0);
    }
  };
  const draw = startTime => {
    if (!__CLIENT__) {
      return;
    }
    var canvas = document.getElementById("canvas") as HTMLCanvasElement;
    var ctx = canvas.getContext("2d");
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.lineWidth = 4;
    //画田字格
    var drawBoard = function () {
      ctx.setLineDash([]);
      ctx.strokeStyle = "red"; // red
      for (var i = 0; i < BOARD_SIZE; i++) {
        ctx.beginPath();
        ctx.moveTo(PADDING + i * BOARD_CELL_SIZE, PADDING);
        ctx.lineTo(
          PADDING + i * BOARD_CELL_SIZE,
          BOARD_CELL_SIZE * BOARD_SIZE - PADDING
        );
        ctx.stroke();
        ctx.moveTo(PADDING, PADDING + i * BOARD_CELL_SIZE);
        ctx.lineTo(
          BOARD_CELL_SIZE * BOARD_SIZE - PADDING,
          PADDING + i * BOARD_CELL_SIZE
        );
        ctx.stroke();
      }

      // X
      ctx.beginPath();
      ctx.setLineDash([5, 15]);
      ctx.moveTo(0, 0);
      ctx.lineTo(600, 600);
      ctx.stroke();
      // X
      ctx.beginPath();
      ctx.setLineDash([5, 15]);
      ctx.moveTo(600, 0);
      ctx.lineTo(0, 600);
      ctx.stroke();

      ctx.closePath();
    };
    drawBoard();
    const drawWord = thepaths => {
      if (startTime !== time) {
        return;
      }
      var vertices = [];
      if (thepaths.length > 0) {
        vertices = thepaths.shift();
      } else {
        return;
      }
      // calculate incremental points along the path
      var points = calcWaypoints(vertices);
      // extend the line from start to finish with animation
      // calc waypoints traveling along vertices
      function calcWaypoints(vertices) {
        var waypoints = [];
        var fps = 50;
        var len = vertices.length;
        if (len > 3) {
          fps = 40;
        }
        for (var i = 1; i < len; i++) {
          var pt0 = vertices[i - 1];
          var pt1 = vertices[i];
          var dx = pt1[0] - pt0[0];
          var dy = pt1[1] - pt0[1];
          for (var j = 0; j < fps; j++) {
            var x = pt0[0] + (dx * j) / fps;
            var y = pt0[1] + (dy * j) / fps;
            waypoints.push({
              x: x,
              y: y
            });
          }
        }
        return waypoints;
      }
      var t = 1;
      function animate() {
        if (startTime !== time) {
          cancelAnimationFrame(reqFrame);
          return;
        }
        ctx.lineCap = "round";
        ctx.lineWidth = 15;
        ctx.strokeStyle = "#000";
        ctx.beginPath();
        ctx.moveTo(points[t - 1].x, points[t - 1].y);
        ctx.lineTo(points[t].x, points[t].y);
        ctx.stroke();

        if (t < points.length - 1) {
          reqFrame = requestAnimationFrame(animate);
        } else {
          drawWord(thepaths);
        }
        t++;
      }
      animate();
    };
    if (char && paths && paths.length) {
      drawWord(paths);
    }
  };
  let audioRef = React.createRef() as any;
  useEffect(() => {
    time = new Date().getTime();
    draw(time);

    try {
      if (!char) return;
      let py = transPinyin(char);
      setPinyin(py);
      let tone = converttoNoTone(py).trim();
      let t = `https://raw.githubusercontent.com/icai/tts-chinese/master/dist/${tone}.mp3`;
      var n = audioRef;
      n.setAttribute("datasrc", t);
    } catch (e) { }
  }, [char]);

  useEffect(() => {
    let path = decodeURIComponent(location.hash.slice(2));
    onSearch(path);
  }, []);

  const onVoiceHover = () => {
    var n = audioRef;
    n.src = n.getAttribute("datasrc");
  };

  const onVoiceClick = () => {
    var n = audioRef;
    n.loop = false;
    n.play();
  };

  return (
    <Pane width="800px" height="100%" margin="auto">
      <WriteCharactorSearch
        char={char}
        onSearch={onSearch}
      ></WriteCharactorSearch>
      <Pane
        className="clearfix"
        width={"600px"}
        margin={"auto"}
        paddingTop={50}
      >
        <canvas id="canvas" width={LAYOUT_SIZE} height={LAYOUT_SIZE}></canvas>
      </Pane>
      <Pane lineHeight="32px" fontSize="18px">
        拼 音: {pinyin}{" "}
        <VoiceComponent
          style={{ verticalAlign: "middle", cursor: "pointer" }}
          onMouseEnter={onVoiceHover}
          onClick={onVoiceClick}
        ></VoiceComponent>
      </Pane>
      <div className="text-right">
        <a
          href="https://github.com/w3cub/w3cubtools-md/issues"
          target="_blank"
          className="word"
        >
          Report an error
          </a>
      </div>
      
      <Pane>
        <Pane >
          <Tablist marginBottom={16} flexBasis={240} textAlign={'right'} marginRight={24}>
            {state.tabs.map((tab, index) => (
              <Tab
                key={tab}
                id={tab}
                onSelect={() => setState({ selectedIndex: index })}
                isSelected={index === state.selectedIndex}
                aria-controls={`panel-${tab}`}
              >
                <h3>{tab}</h3>
              </Tab>
            ))}
          </Tablist>
          <Pane padding={16} background="#fff" flex="1">
            {state.tabs.map((tab, index) => (
              <Pane
                key={tab}
                id={`panel-${tab}`}
                role="tabpanel"
                aria-labelledby={tab}
                aria-hidden={index !== state.selectedIndex}
                display={index === state.selectedIndex ? 'block' : 'none'}
              >
                <Pane display="flex" alignItems="flex-start" >
                {state[tab].map((col, ix) => (
                  <Pane
                    key={ix}
                    flex="1 1 50%"
                    flexWrap="wrap"
                    display="flex"
                    padding={10}
                    marginX={10}
                    alignItems="flex-start"
                    justifyContent="center"
                  >
                    {col.map((line, li) => (
                      <Pane
                        key={li}
                        flex="1 1 100%"
                        display="flex"
                        alignItems="flex-start"
                        justifyContent="space-between"
                      >
                        {
                          line.map((word, wi) => (
                            <Pane
                              key={wi}
                              margin={5}
                              display="flex"
                              alignItems="center"
                              justifyContent="center"
                              padding={10}
                              height={70}
                              width={54}
                              style={{ border: '1px solid #69BF61', borderRadius: '5px'}}
                            >
                              {word}
                            </Pane>
                          ))
                        }
                      </Pane>
                    ))}
                  </Pane>
                ))}
                </Pane>
              </Pane>
            ))}
          </Pane>
        </Pane>
        {commonWords.split("").map(word => {
          return (
            <a
              href="javascript:;"
              className="word"
              key={word}
              onClick={() => {
                toTop();
                setTimeout(() => {
                  onSearch(word);
                }, 500);
              }}
            >
              {word}
            </a>
          );
        })}
      </Pane>
      <div style={{ display: "none" }}>
        <audio
          id="audio"
          ref={input => {
            audioRef = input;
          }}
        ></audio>
      </div>

      <style jsx>{`
          .word {
            margin-right: 20px;
            line-height: 25px;
            color: #000;
          }
        `}</style>
    </Pane>
  );
}
